import supabase from "./supabase.js";





// 查询全部
const supaAPI = async () => {
    const {
        data: user
    } = await supabase
        .from('user')
        .select('*')
    return user;
}

// 插入数据
const insetAPI = async (r) => {
    const {
        data
    } = await supabase
        .from('user')
        .insert(r)
        .select()
    return data
}

// 查询单个指定数据
const selAPI = async (n) => {
    const {
        data
    } = await supabase
        .from('user')
        .select('*')
        .eq('Byid', n)
        .single();
    console.log('到我了');

    return data
}

// 删除指定行
const delAPI = async d => {
    const {
        data,
        error
    } = await supabase
        .from('user') // 替换为你的表名
        .delete() // 执行删除操作
        .eq('Byid', d); // 条件：删除 id 为 1 的行
}

// 更新指定字段（例如：更新 id 为 1 的用户的 email 字段）
const updataAPI = async (up, newdate) => {
    const {
        data,
        error
    } = await supabase
        .from('user') // 替换为你的表名
        .update(newdate)
        .eq('Byid', up) // 设置更新条件：id 为 1 的用户 
        .select() // 这里返回更新后的数据 

    return data
}




// ———————————文章Api————————————

// 查询用户全部文章
const selArtAPI = async (Byid) => {
    const {
        data
    } = await supabase
        .from('articles')
        .select('*')
        .eq('author_Byid', Byid)

    return data
}


// 查询单个指定文章
const selOneAPI = async (art_uuid) => {
    const {
        data
    } = await supabase
        .from('articles')
        .select('*')
        .eq('art_uuid', art_uuid)
        .single();
        console.log(data,'当个');
        
    return data
}

// 删除指定文章
const DelartAPI = async (art_uuid, author_Byid) => {
    try {
        await supabase
            .from('articles') // 替换为你的表名
            .delete() // 执行删除操作
            .eq('art_uuid', art_uuid) // 条件：删除 id 为 1 的行    

        // 获取该用户剩余的文章
        const {
            data,
            error
        } = await supabase.from('articles').select('*').eq('author_Byid', author_Byid);
        if (error) {
            throw error;
        }
        return data;
    } catch (error) {
        console.error('Error deleting and fetching remaining articles:', error);
        return null;
    }
}

// 发布文章
const adArtAPI = async (title, content, DateTime, articleId) => {

    // const articleId = this.$route.params.id;  
    const {
        data,
        error
    } = await supabase
        .from("articles")
        .insert([{
            title: title,
            content: content,
            created_at: DateTime,
            author_Byid: articleId,
            image: 'https://img.daisyui.com/images/stock/photo-1635805737707-575885ab0820.webp',
        }])

    if (error) {
        console.error('插入文章失败：', error);
    } else {

        console.log('插入文章成功：', data);
        return 'ok'
    }


    //   .eq("author_Byid", articleId);

}

export {
    supaAPI,
    insetAPI,
    selAPI,
    delAPI,
    updataAPI,
    selArtAPI,
    selOneAPI,
    DelartAPI,
    adArtAPI
}