import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    theme: 'light', // 默认主题为 light
    backtext: '',
    user: {},
    posts: [],
    artview:{}
  },
  mutations: {
    setTheme(state, newTheme) {
      state.theme = newTheme;
      document.getElementById('app').setAttribute('data-theme', newTheme);
    },
    setbacktext(state, newBacktext) {
      state.backtext = newBacktext
    },
    setUser(state, newUser) {   // 用户用资料
      state.user = newUser
    },
    setPosts(state, newPosts) {   //用户文章
      state.posts = newPosts
    },
    setView(state, newartView) {   //用户单个文章
      state.artview = newartView
    }
  },
  plugins: [
    createPersistedState({
      storage: window.localStorage, // 使用 localStorage 来持久化主题状态
      paths: ['theme', 'backtext', 'user', 'posts','artview'] // 持久化数据状态
    })
  ]
});