<template>
  <div class="container mx-auto p-4">
    <!-- 返回按钮 -->
    <BackHome></BackHome>

    <!-- 文章头部信息 -->
    <header class="text-center my-6">
      <h1 class="text-4xl font-bold mb-2">{{ article.title }}</h1>
      <p class="text-gray-500">
        {{ this.$route.params.Byname }} - {{ article.created_at }}
      </p>
    </header>

    <!-- 文章内容 -->
    <article class="prose mx-auto mb-8">
      <p>{{ article.content }}</p>
    </article>

    <!-- 点赞按钮 -->
    <div class="flex justify-center items-center mb-4">
      <button @click="likeArticle" class="btn btn-primary">
        👍 点赞 {{ article.likes }}
      </button>
    </div>

    <!-- 评论区 -->
    <div class="card bg-base-100 shadow-xl p-6">
      <h2 class="text-2xl font-bold mb-4">发表评论</h2>
      <textarea
        v-model="newComment"
        placeholder="请输入评论内容..."
        class="textarea textarea-bordered w-full mb-4"
      ></textarea>
      <button @click="submitComment" class="btn btn-secondary">提交评论</button>

      <!-- 评论列表 -->
      <div class="mt-6">
        <h3 class="text-xl font-bold mb-2">评论</h3>
        <ul>
          <li v-for="(comment, index) in comments" :key="index" class="mb-2">
            <div class="card bg-gray-100 shadow-md p-4">
              {{ comment }}
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BackHome from "@/components/BackHome.vue";
import { selOneAPI } from "@/archive/user.js";

export default {
  name: "ArticleView",
  components: {
    BackHome,
  },
  data() {
    return {
      // art_uuid: this.$route.params.art_uuid,
      // author_Byid: this.$route.params.author_Byid,
      article: {},
      comments: [], // 评论列表
      newComment: "", // 新评论内容
    };
  },
  computed: {
    formattedDate() {
      const date = new Date(this.article.date);
      return date.toLocaleDateString();
    },
  },
  methods: {
    likeArticle() {
      this.likes++;
    },
    submitComment() {
      if (this.newComment.trim() === "") {
        alert("评论内容不能为空！");
        return;
      }
      this.comments.push(this.newComment);
      this.newComment = ""; // 清空评论框
    },
  },
  async created() { 
    console.log(
      this.$store.state.artview, 
      "artOnedata一个文章"
    );
    this.article = this.$store.state.artview;
    // this.user = data
    // this.posts = artdata
  },
};
</script>

<style scoped>
.container {
  max-width: 800px;
}
</style>
