// src/router/index.js
import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '@/components/HelloWorld.vue'; // 主页组件
import EditProfile from '@/components/EditProfile.vue'; // 编辑个人资料页面
import Password from '@/components/Password.vue'; // 修改密码页面
import ArticleView from '@/components/ArticleView.vue'; // 文章页面
import AddArticle from '@/components/AddArticle.vue'; // 发布文章页面

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
    },
    {
      path: '/edit-profile',
      name: 'EditProfile',
      component: EditProfile,
    },
    {
      path: '/update-Password',
      name: 'UpdatePassword',
      component: Password,
    },
    {
      path: '/artic-leView',
      name: 'ArticleView',
      component: ArticleView,
    },
    {
      path: '/Add-article',
      name: 'AddArticle',
      component: AddArticle,
    },
  ],
});