<template>
  <div class="container mx-auto p-4">
    <!-- Add the return to home button with a left arrow icon -->
    <div class="navbar bg-base-100">
      <div class="navbar-start">
        <div class="dropdown">
          <div
            tabindex="0"
            role="button"
            class="btn btn-ghost btn-circle"
            @click="goHome"
          >
            <!-- Left arrow icon -->
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="h-6 w-6 mr-2"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M15 19l-7-7 7-7"
              />
            </svg>
          </div>
        </div>
      </div>
      <div class="navbar-center">
        <p class="btn btn-ghost text-xl">{{backtext}}</p>
      </div>
      <div class="navbar-end">
        <button class="btn btn-ghost btn-circle">
          <div class="indicator"></div>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BackHome",
  data() {
    return {};
  },
  methods: {
    goHome() {
      this.$router.push({ name: "HelloWorld" }); // Redirects to the homepage (Blog)
    },
  },
  computed: {
    backtext() {
      return this.$store.state.backtext;
    },
  }
};
</script>

<style>
.p-4 {
  padding: 0.5rem;
}
</style>
